.left-one {
    /* background: #373030; */
    display: flex;
    justify-content: center;
    width: 70%;
    height: 70vh;
}

.img-div{
    width: 60%;
    height: 100%;
}

.left-one img {
    width: 100%;
    height: 100%;
    background-position: center;
    background-size: cover;
}

@media (min-width: 481px) and (max-width: 767px) {
    .left-one {
        width: 100%;
        height: 80vh;
        margin: 2% 0;
    }

    .img-div{
        width: 80%;
        height: 100%;
    }
}