@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');

.section {
   display: flex;
   flex-direction: column;
}

.section-one {
   background: #434040;
   display: flex; 
   padding: 4%;
}



.right-one {
   display: flex;
   flex-direction: column;
   width: 50%;
   padding: 2% 7%; 
}

.right-one h2 {
   color: #E7D6D3;
   font-family: 'Roboto', sans-serif;
   font-size: 1.2rem;
   font-weight: 300;
}

.right-one input{
   margin: 20px 0;
   width: 100%;
}

.right-one button {
   background: #8B6A63;
   padding: 8px;
   color: #EEE9E9;
   outline: none;
   border: none;
   width: 20%;
   cursor: pointer;
}

.input {
   display: flex;
}

input {
   padding: 16px 0;
   padding-right: 20px; 
   outline: none;
   border: #C4C4C4 1px solid;
   border-left: none;
   background: #746D6D;
   color: #DFCAC5;
   font-family: 'Roboto', sans-serif;
}
input::placeholder {
   color: #DFCAC5;
   font-size: 16px;
}

.camera {
   color: #DFCAC5;
   align-self: center;
   padding: 14px 8px;
   background: #746D6D;
   border: #C4C4C4 1px solid;
   border-right: none;
}

@media (min-width: 481px) and (max-width: 767px) {
    .section-one {
        flex-direction: column-reverse;
     }

     .right-one {
      width: 100%;
  }

  .right-one h2 {
      font-size: 1.4rem;
  }

  .right-one .search-query {
          display: flex;
  }

  .right-one button {
      padding: 0; 
      border: none;
      width: 30%;
      border: #C4C4C4 1px solid;
      border-left: none;
      font-size: 16px;
      height: 58px;
      align-self: center;
  }

  input {
      padding: 20px 0;
  }

  .camera {
      padding: 18px 13px;
  }

  input::placeholder {
      font-size: 18px;
  }

  .right-one {
      flex-direction: column;
  }
}