form .label {
    margin: 20px 0;
}

form input {
    width: 100%;
    outline: none;
    padding: 20px 0;
    border: #DAD1D1 1px solid;
    background: #AE918A;
    margin: 10px 0;
    font-size: 20px;
}

@media (min-width: 768px) and (max-width: 1024px) {

    form .label {
        margin: 15px 0;
    }

    form input {
        padding: 10px 0;
        margin: 5px 0;
    }
}

@media (min-width: 481px) and (max-width: 767px) { 
    form .label {
        font-size: 22px;
    }
}