.header {
    background: #222020;
    display: flex;
    flex-direction: row;
}

.image {
    margin-left: 5%;
}

@media (min-width: 768px) and (max-width: 1024px) {
  
    .header img {
          width: 100%;  
         height: 100%;   
    }

    .header .image { 
           width: 30%;   
        } 
   
    
  }


@media (min-width: 481px) and (max-width: 767px) {

    .header .image { 
        display: none;   
     
  } 
}