@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');

.section-two {
    display: flex;
    padding: 5% 8%;
    background: #C4C4C4;
    font-family: 'Roboto', sans-serif;
    color: #666161;
}

.left-two {
    width: 50%;
}

.left-two h1 {
    color: #504747;
}

.left-two .section-div {
    display: flex;
    margin: 5% 0;
}

.left-two .section-div .title {
    font-weight: bold;
    font-size: 20px;
}

.left-two .section-div .title-info {
    margin-left: 10px;
}

.right-two {
    width: 50%;
}


.right-two .title {
    font-weight: bold;
    font-size: 20px;
}

.right-two .title-info {
    width: 90%;
    margin-top: 5%;
    line-height: 1.7;
}

@media (min-width: 481px) and (max-width: 767px) {
    .section-two {
        flex-direction: column;
        padding: 10%;
    }

    .left-two {
        width: 100%;
    }

    .left-two h1 {
        font-size: 40px;
    }

    .left-two .section-div .title {
        font-size: 24px;
    }
    
    .left-two .section-div .title-info {
        font-size: 20px;
        align-self: center;
    }
    .right-two {
        width: 100%;
    }

    .right-two .title {
        font-size: 24px;
    }
    
    .right-two .title-info {
        font-size: 20px;
    }
}