@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@500&family=Roboto:wght@400;700&display=swap');


.welcome {
    padding: 20px;
    width: 40%;
}
.welcome h1 {
    font-family: 'Raleway', sans-serif;
    color: #9F8B8B;
    font-size: 4.3rem;
    letter-spacing: 3px;
    font-weight: 400;
    margin-top: 45px;
    line-height: 1.3;
}

.welcome h3 {
    font-family: 'Roboto', sans-serif;
    color: #E7D6D3;
    margin: 80px 0;
}

.welcome .btn {
    font-family: 'Roboto', sans-serif;
    padding: 15px 68px;
    background: #A37F77;
    color: #EEE9E9;
    font-size: 1.5rem;
    border-radius: 45px;
    cursor: pointer;
}

.welcome .btn:hover {
    background: #856963;
}

.welcome p {
    display: none; 
    font-family: 'Roboto', sans-serif;
    color: #E7D6D3;
    margin: 20px 0;
    font-size: 23px;
}

.welcome .extra-content {
    margin:  50px 0;
}

.welcome .btn-two {
    display: none; 
    background: #A37F77;
    color: #EEE9E9;
    padding: 15px 40px;
    border: none;
    border-radius: 30px;
    cursor: pointer;
    font-size: 18px;
}

.welcome .btn-two:hover {
    background: #856963;
}


@media (min-width: 768px) and (max-width: 1024px) {

    .welcome {
        padding: 10px;
        margin-left: 70px;
    }

    .welcome h1 {
        font-size: 3.2rem;
        margin-top: 0;
        padding: 10px 0;
        }

        .welcome h3 {
            margin: 40px 0;
        }

}

@media (min-width: 481px) and (max-width: 767px) {

    .welcome {
        width: 70%;
        margin: 0 0 40px 50px;
    }

    .welcome h1 {
        width: 50%;
        margin-top: 0;
        font-size: 3.5rem;
    }
    .welcome h3 {
        margin: 35px 0;
    }

    .welcome .btn {
        font-size: 1.6rem;
        
    }

    .welcome p {
        display: flex; 
        font-size: 22px;
    }
    
    .welcome .btn-two {
        display:flex; 
    }
    
  
}