* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
 .App {
     width: 100%;
     height: 100%;
 }




.nav {
    background: #222020;
    
}

.logo {
    color: #B79F9A;
    font-family: 'Sacramento', cursive;
    font-size: 45px;
}

.nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 3% 5%;
}


a {
    text-decoration: none;
    color: #9F8B8B;
     }

a:hover {
    color: #E7D6D3;
}

.link {
    font-family: 'Roboto', sans-serif;
}

.btn {
    background: #A37F77;
    color: #EEE9E9;
    padding: 10px 30px;
    border: none;
    border-radius: 30px;
    cursor: pointer;
}
.btn:hover {
    background: #856963;
}

.hamburger {
    flex-direction: column;
    display: none;
}

.lines {
    background: #B79F9A;
    
    height: 2px;
    margin: 10px 0;
    transition: all 0.2s ease;
}

.menu {
    display: none;
    }

.inactive-ham {
    width: 50px;
}



.inactive-1 {
    transform: translateY(0) rotate(0);
    transition: transform 0.3s ease;
}

.inactive-2 {
    transform: translateY(0) rotate(0);
    transition: transform 0.3s ease;
}


.active-1 {
    transform:  translateY(10px) rotate(45deg);
}

.active-2 {
    transform: translateY(-10px) rotate(-45deg);
}

.active-ham {
    width: 40px;
}


@media (min-width: 768px) and (max-width: 1024px) {
  
    a {
     font-size: 14px;
         }

         .nav {
            padding: 2% 5%;
        }   

        .menu {
        display: none;
        }
        
    
  }

  @media (min-width: 481px) and (max-width: 767px) {
  
    .link {
        display: none;
    }

    .hamburger {
        display: flex;
    }

    
.nav {
    padding: 3% 8%;
    padding-top: 8%;
    height: 10vh;
}


    
.logo {
    font-size: 35px;
}

.menu  {
    background: #222020;
    z-index: 2; 
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 38px;
     transition: all 0.2s ease;
     position: absolute;
     top: 10vh;
}

.menu .item {
    cursor: pointer;
    margin: 10% 0;
}

.inactive {
    opacity: 0;
    height: 0; 
    width: 0;
    transition: transform 0.3s ease-in-out;
    
}

.active{
     opacity: 1; 
     transform: translateY(180deg);
     height: 100vh; 
     width: 100vw; 
     display: flex;
}

 }

