}

input {
   padding: 16px 0;
   padding-right: 20px; 
   outline: none;
   border: #C4C4C4 1px solid;
   border-left: none;
   background: #746D6D;
   color: #DFCAC5;
   font-family: 'Roboto', sans-serif;
}
input::-webkit-input-placeholder {
