}
input::-ms-input-placeholder {
   color: #DFCAC5;
   font-size: 16px;
}
input::placeholder {
   color: #DFCAC5;
   font-size: 16px;
}

.camera {
   color: #DFCAC5;
   align-self: center;
   padding: 14px 8px;
   background: #746D6D;
   border: #C4C4C4 1px solid;
   border-right: none;
}

