@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@500&family=Roboto:wght@400;700&display=swap');

.left-three {
    width: 50%;
    padding: 3% 5%;
}

.left-three p {
    margin-top: 18px;
    width: 60%;
}

.left-three button {
    font-family: 'Roboto', sans-serif;
    color: #584844;
    background: #C3BBBB;
    padding: 14px;
    width: 28%;
    outline: none;
    font-size: 20px;
    margin-top: 5%;
    border: none;
    cursor: pointer;
}

.left-three button:hover {
    background: #DAD1D1;
}

@media (min-width: 768px) and (max-width: 1024px) {
    .left-three button {
        width: 35%;
    }
}

 @media (min-width: 481px) and (max-width: 767px) { 
     .left-three { 
         width: 100%; 
         padding: 10%;
     } 

     .left-three h1 {
        font-size: 42px;
    }
    
    .left-three p {
        font-size: 22px;
        width: 100%;
    }

    .left-three button {
        font-size: 28px;
        width: 50%;
        transform: translateX(50%);
    }
 }  